<template>
    <div class="barCharts">
        <div id="barCharts" ref="barCharts"></div>
    </div>
</template>

<script>
  import * as  echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      color: ['#3398DB', '#f2e84e'],
      tooltip: {
        trigger: 'axis',            //坐标轴触发，主要在柱状图，折线图等会使用类目轴的图表中使用
        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
          type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
        },
        // formatter: '{a}, {b}，{c}，{d}，{e}'
      },
      grid: {
        left: '2%',
        top: '10%',
        right: '8%',
        bottom: '5%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          name: '年龄',
          data: [],
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false
          },
          axisLabel: { //设置x轴的字
            show: true,
            // interval:0,//使x轴横坐标全部显示
            // rotate:50,//旋转
            textStyle: {//x轴字体样式
              // color: "#ff8000",
              margin: 15
            }
          },

        }
      ],
      yAxis: [
        {
          name: '人数',
          type: 'value',
          axisLabel: { //设置y轴的字
            show: true,
            interval: 0,//使y轴横坐标全部显示
            textStyle: {//y轴字体样式
              // color: "#ff8000",
              margin: 15
            }
          },
          axisTick: { //y轴刻度线
            show: false
          },
          axisLine: { //y轴
            show: false
          },
        }
      ],
      series: [
        {
          name: '',
          type: 'bar',
          // barWidth: '60%',
          barMaxWidth: '28',
          // barMinWidth:'20',
          data: [],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 1, 0, 0,
                [
                  { offset: 0, color: 'rgba(84,110,211,1)' },                   //柱图渐变色
                  { offset: 0.3, color: 'rgba(84,110,211,0.8)' },                 //柱图渐变色
                  { offset: 1, color: 'rgba(84,110,211,0.5)' },                   //柱图渐变色
                ]
              )
            },
          },
        }
      ]
    };


  export default {
    name: "barCharts",
    props: {
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      zj:{
        type:String,
        default:()=>{
          return ''
        }
      },
      seriesData: {
        /**
         * 默认形式
         * seriesData:[{
         *    data:‘’,
         *    _x:‘’
         * }]
         */
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示
      _zb: {
        /**
         * 默认形式
         * :['X','Y']
         */
        type: Array,
        default: () => {
          return ['X', 'Y']
        }
      },
      //柱状图渐变颜色
      barColor: {
        type: Array,
        default: () => {
          return [
            { offset: 0, color: 'rgba(84,110,211,1)' },                   //柱图渐变色
            { offset: 0.3, color: 'rgba(84,110,211,0.8)' },                 //柱图渐变色
            { offset: 1, color: 'rgba(84,110,211,0.5)' },                   //柱图渐变色
          ]
        }
      },

    },
    watch: {

    },
    data() {
      return {
        option: optionTemp,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        this.option.series[0].itemStyle.normal.color.colorStops = this.barColor;
        this.option.xAxis[0].name = this._zb[0]
        this.option.yAxis[0].name = this._zb[1]
        this.option.series[0].data = []
        this.option.series[0].name = this._zb[1]
        if (this.seriesData.length>0){
          this.option.xAxis[0].data = []
          for (let i = 0; i < this.seriesData.length; i++) {
            this.option.series[0].data.push({value: this.seriesData[i].data, id: this.seriesData[i].id})
            this.option.xAxis[0].data.push(this.seriesData[i]._x)
          }
        }
        let echart = require('echarts');
        let mychart = echart.init(this.$refs.barCharts, );
        window.addEventListener("resize", mychart.resize);
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index,$this.zj)
          // window.console.log(params);
        });

      },
    }

  }
</script>

<style scoped lang="scss">
    .barCharts {
        height: 100%;
        #barCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>